import axios from 'axios';
import authHeader from './auth-header';


const API_URL = 'https://plataformatransporte.herokuapp.com/';

class UserService {

  getAccess() {
    return axios.get(API_URL + 'user/me', {headers: authHeader()}).then( response => {
      localStorage.setItem("typeAccess", JSON.stringify(response.data.body.type));
      return response.data.body.type;
    });
  }

  checkAccess(){
    return axios.get(API_URL + 'user/me', {headers: authHeader()}).then( response => {
      const tpAccess = JSON.stringify(response.data.body.type);
      if (tpAccess === localStorage.getItem("typeAccess")){
        return true;
      }else{
        return false;
      }
    });
  }
}

export default new UserService();