import React from "react";
import { Link } from "react-router-dom";
import { Navbar, Nav } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "../../assets/admin.styles.css";
import logo from "../../assets/carryflow_logo_white_sm.png";
import MenuLatAdmin from "../global/menu.lat.admin";
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import * as Icons from '@fortawesome/fontawesome-free-solid';

// <FontAwesomeIcon icon={Icons.faBars} size="2x" />



const NavAdmin = () => {

    return(
        <div>
        
        <Navbar className="navbar-color" expand="lg">
        
        <Navbar.Brand href="#void">
            <img src={logo}
            alt="carryflow"/>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
                <Link className="nav-link-cl" to="/admin">Home</Link>
                <Link className="nav-link-cl" to="/admin/cadastro/users">Cadastro</Link>
            </Nav>
        </Navbar.Collapse>
        </Navbar>
        </div>
    )
};

export default NavAdmin;