import React, { Component } from "react";
import UserService from "../services/user.service"
import NavCarry from "./global/nav.carry";


export default class CarrySchedule extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
      if(localStorage.getItem("typeAccess") === '"CARRY"'){
        UserService.checkAccess().then(response => {
          if(!response){
            this.props.history.push(`/`);
          }
        });
      }else{
        this.props.history.push(`/`);
      }
    }

    render(){
        return(
          <div>
            < NavCarry />
            <div className="titulo">
              <h2>Agendar Carregamento</h2>
            </div>

            <div className="col-12 row">
              <div className="col-2"></div>
              <div className="col-8 form-group panel-form row">
                <div className="col-12"><h4>Ticket #18293</h4></div>
                <div className="col-12"><h5>Produto: Lacre</h5></div>
            
                <div className="col-3">
                  <label htmlFor="dtCarr">Data de Carregamento
                    <input className="form-control" name="dtCarr" type="text" />
                  </label>
                </div>

                <div class="col-3">
                  <label htmlFor="qtde">Quantidade
                    <input className="form-control" name="qtde" type="text" />
                  </label>
                </div>

                <div className="col-9">
                  <label htmlFor="contato">Contato
                    <input className="form-control" name="contato" type="text" />
                  </label>
                </div>

                <div className="col-3">
                  <label htmlFor="telefone">Telefone
                    <input className="form-control" name="telefone" type="text" />
                  </label>
                </div>

                <div className="col-9">
                  <label htmlFor="motorista">Motorista
                    <input className="form-control" name="motorista" type="text" />
                  </label>
                </div>

                <div className="col-3">
                  <label htmlFor="cpf">CPF
                    <input className="form-control" name="cpf" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="plc_cam">Placa
                    <input className="form-control" name="plc_cam" type="text" />
                    </label>
                </div>

                <div className="col-2">
                  <label htmlFor="uf_cam">UF
                    <input className="form-control" name="uf_cam" type="text" />
                  </label>
                </div>

                <div className="col-8"></div>

                <div className="col-2">
                  <label htmlFor="plc_reb1">Placa Reb. 1
                    <input className="form-control" name="plc_reb1" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="uf_reb1">UF Reb. 1
                    <input className="form-control" name="uf_reb1" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="plc_reb2">Placa Reb. 2
                    <input className="form-control" name="plc_reb2" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="uf_reb2">UF Reb. 2
                    <input className="form-control" name="uf_reb2" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="plc_reb3">Placa Reb. 3
                    <input className="form-control" name="plc_reb3" type="text" />
                  </label>
                </div>

                <div className="col-2">
                  <label htmlFor="uf_reb3">UF Reb. 3
                    <input className="form-control" name="uf_reb3" type="text" />
                  </label>
                </div>

                <div className="col-12">
                  <div className="panel-bottom">
                    <button type="button" name="btEnviar" className="btn btn-primary btn-md btn-block float-right">Agendar</button>
                  </div>

                  <div className="panel-bottom">
                    <button type="button" name="btEnviar" className="btn btn-secondary btn-md btn-block float-right">Cancelar</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
    }
}