import React, { Component } from "react";
import UserService from "../services/user.service"
import NavAdmin from "./global/nav.admin";


export default class CadAdmin extends Component {

  constructor(props) {
    super(props);

    this.state = {
      cellPhone: "",
      cpf: "",
      email: "",
      name: "",
      password: "",
      profile: "",
      provider: ""
    };

    this.state = {
        content: ""
    };
  }

  cadastrarLogin = () => {}

  onChangeName = (e) => {
    this.setState({
      name: e.target.value
    })
  }

  showBiros = () => {
    console.log(this.state.name);
  } 


  componentDidMount() {
    if(localStorage.getItem("typeAccess") === '"ADMIN"'){
      UserService.checkAccess().then(response => {
        if(!response){
          this.props.history.push(`/`);
        }
      });
    }else{
      this.props.history.push(`/`);
    }
  }

  render(){
    return(
      <div>
        <NavAdmin />
        <div className="titulo">
          <h2>Cadastrar Usuário</h2>
        </div>

        <div className="col-12 row">
          <div className="col-2"></div>

          <div className="col-8 form-group panel-form row">
            <div className="col-8">
              <label htmlFor="name">Nome
                <input
                  type="text"
                  className="form-control"
                  name="name"
                  value={this.state.name}
                  onChange={this.onChangeName}
              />
              </label>
            </div>

            <div className="col-4">
              <label htmlFor="cpf">CPF
                <input className="form-control" name="cpf" type="text" />
              </label>
            </div>

            <div className="col-6">
              <label htmlFor="email">e-mail
                <input className="form-control" name="email" type="email" />
              </label>
            </div>

            <div className="col-3">
              <label htmlFor="cel">Celular
                <input className="form-control" name="cel" type="email" />
              </label>
            </div>
            
            <div className="col-3">
              <label htmlFor="tipoAcesso">Tipo de acesso
                <select className="custom-select" name="tipoAcesso">
                  <option value="CARRY">Transportadora</option>
                  <option value="CLIENT">Embarcador</option>
                  <option value="ADMIN">Administrador</option>
                </select>
              </label>
            </div>

            <div className="col-4">
              <label htmlFor="senha">Senha
                <input className="form-control" name="senha" type="password" />
              </label>
            </div>

            <div className="col-4">
              <label htmlFor="senhaConf">Confirme a senha
                <input className="form-control" name="senhaConf" type="password" />
              </label>
            </div>
            <div className="col-4"></div>
            <div className="clearfix"></div>

            <div className="form-group col-2 sp-top">
              <button className="btn btn-primary btn-block"
              onClick={() => this.showBiros}>
                <span>Cadastrar</span>
              </button>
            </div>

          </div>
        </div>
      </div>
    );
  }
}