import axios from "axios";
import UserService from "./user.service";

const API_URL = "https://plataformatransporte.herokuapp.com/";

class AuthService {
  login(email, password) {
    return axios
      .post(API_URL + "auth/login", {
        email,
        password
      })
      .then(response => {
        if (response.data.body.accessToken) {
          localStorage.setItem("user", JSON.stringify(response.data.body.accessToken));
          return UserService.getAccess();
        }
      });
  }

  logout() {
    localStorage.removeItem("user");
    localStorage.removeItem("typeAccess");
  }

  getCurrentUser() {
    return JSON.parse(localStorage.getItem('user'));;
  }
}

export default new AuthService();