import React, { Component } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import Admin from "./components/admin.component";
import CadAdmin from "./components/admin.reg.component";

import Carry from "./components/carry.component";
import CarrySchedule from "./components/carry.schedule";
import CarryList from "./components/carry.list";

import Client from "./components/client.component";

import Login from "./components/login.component";

class Rotas extends Component{
    render(){
        return(
        <BrowserRouter>
            <Switch>
                <Route path="/" exact={true} component={ Login } />
                <Route exact path="/admin" component={ Admin } />
                <Route exact path="/admin/cadastro/users" component={ CadAdmin } />

                <Route exact path="/carry" component={ Carry } />
                <Route exact path="/carry/cargas" component={ CarryList } />
                <Route exact path="/carry/agendamento" component={ CarrySchedule } />

                <Route exact path="/client" component={ Client } />
                <Route exact path="/client/CadastroCarregamento" component={ Client } />

                <Route path="*" component={ Login } />
            </Switch>
        </BrowserRouter>
        )
    }
}

export default Rotas;