import React, { Component } from "react";
import UserService from "../services/user.service"
import NavCarry from "./global/nav.carry";


export default class Carry extends Component {
    constructor(props) {
        super(props);
        
        this.state = {
            content: ""
        };
    }

    componentDidMount() {
      if(localStorage.getItem("typeAccess") === '"CARRY"'){
        UserService.checkAccess().then(response => {
          if(!response){
            this.props.history.push(`/`);
          }
        });
      }else{
        this.props.history.push(`/`);
      }
    }

    render(){
        return(
            <div>
                < NavCarry />
            </div>
        );
    }
}